<template>
  <div>
    <div class="vx-row">
      <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
        <statistics-card-line
          icon="UsersIcon"
          :statistic="usersData.length"
          statisticTitle="Utilisateurs"
        ></statistics-card-line>
      </div>
      <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
        <statistics-card-line
          icon="DollarSignIcon"
          :statistic="realStocksCost + ' TND'"
          statisticTitle="Stock Reel"
          color="success"
        />
      </div>
      <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
        <statistics-card-line
          icon="ShoppingCartIcon"
          :statistic="Invoices.length"
          statisticTitle="Factures"
          color="danger"
        />
      </div>
      <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
        <statistics-card-line
          icon="ShoppingCartIcon"
          :statistic="Rentals.length"
          statisticTitle="Bon location"
          color="warning"
        />
      </div>
    </div>
    <div class="vx-row">
      <!-- Stock Reel -->
      <div class="vx-col md w-full mb-base  md:w-1/2">
        <vx-card title="Stock Reel">
          <vue-apex-charts
            type="bar"
            height="350"
            :options="invoice.chartOptions"
            :series="invoice.series"
          ></vue-apex-charts>
        </vx-card>

      </div>
      <div class="vx-col md w-full mb-base mt-4 md:w-1/2">
        <vx-card title="Factures">
        <vue-apex-charts type="pie" height="350" :options="invoiceChart.chartOptions" :series="invoiceChart.series"></vue-apex-charts>
        </vx-card>
      </div>

    </div>
  </div>
</template>
<script>
import StatisticsCardLine from "@/components/statistics-cards/StatisticsCardLine.vue";
import ChangeTimeDurationDropdown from '@/components/ChangeTimeDurationDropdown.vue'
import VueApexCharts from "vue-apexcharts";
import axios from "axios";

export default {
  components: {
    StatisticsCardLine,
    VueApexCharts,
    ChangeTimeDurationDropdown
  },
  data() {
    return {
      invoiceConf: 0,
      invoiceCanc: 0,
      invoiceWay: 0,
      invoiceChart: {
        series: [],
        chartOptions: {
          labels: ['Factures en Attente', 'Factures Confirmes', 'Factures Annules'],
          colors: ['#7367F0', '#28C76F', '#EA5455'],
          responsive: [{
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: 'bottom'
              }
            }
          }]
        }
      },
      invoice: {
        series: [{
          data: [],
          name: "Quantit",
        }],
        chartOptions: {
          colors: ['#7367F0', '#28C76F', '#EA5455', '#FF9F43', '#1E1E1E'],
          plotOptions: {
            bar: {
              horizontal: true,
            }
          },
          dataLabels: {
            enabled: false
          },
          xaxis: {
            categories: [],
          }
        }
      },
      usersData: [],
      realStocks: [],
      realStocksCost: 0,
      Invoices: [],
      Rentals: [],
    };
  },
  mounted() {
    this.Getusers();
    this.GetRealStocks();
    this.GetInvoices();
    this.GetRentals();
  },
  methods: {
    async Getusers() {
      await axios
        .get("/users/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.usersData = res.data;
        });
    },
    async GetRealStocks() {
      var data=[]
      var options= []
      await axios
        .get("/realstocks/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          res.data.map((item) => {
            this.realStocksCost = Number(this.realStocksCost + item.totalPrice);
            data.push(item.quantity)
            options.push(item.item)
          });
          this.invoice.series = [{ data: data }];
          this.invoice.chartOptions = { xaxis: { categories: options } };
        });
    },
    async GetInvoices() {
      await axios
        .get("/invoices/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.Invoices = res.data;
          this.Invoices.map((item)=> {
            if (item.invoice_status == "Confirme") {
              this.invoiceConf += 1
            } else if (item.invoice_status == "En attente") {
              this.invoiceWay += 1
            } else if (item.invoice_status == "Annule") {
              this.invoiceCanc += 1
            }
          })
          this.invoiceChart.series = [ this.invoiceWay , this.invoiceConf, this.invoiceCanc ];
        });
    },
    async GetRentals() {
      await axios
        .get("/rentalVoucher/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.Rentals = res.data;
        });
    },
  },
};
</script>
